import React, { Component } from 'react';
import { Grid, Cell, Card, CardTitle, CardText,} from 'react-mdl';
import { makeStyles } from "react-mdl";




class Customcard extends Component {
  render() {
    return(
      <Card className="card-prop" >
        <CardTitle style={{color: 'black', height: '100px'}}> <h4 style={{fontSize: '25px', fontFamily: 'Anton'}}>{this.props.title}</h4> </CardTitle>
        <img src={this.props.imagesrc} alt={this.props.alttext} width="320" height="300"/>
        <CardText border style={{fontFamily: 'Anton'}}>
        {this.props.text}
        </CardText>
      </Card>
    );
  }
}

export default Customcard;
