import React, { Component } from 'react';
import { Grid, Cell, } from 'react-mdl';
import sentiment from './img/sentiment-analysis.jpg';
import perceptron from './img/perceptron.jpg';
import process from './img/process.jpg';
import calc from './img/calc.jpg';
import graph from './img/graph.jpg';

import Card from './card'


class Projects extends Component {

  render() {
    return(
      <div>
      <Grid
    container
    spacing={4}
    className="gridContainer"
    justify="center"
  >

  <Grid>
  <a href="https://github.com/dsahu040/airline_sentiment_analysis" target="_blank" class="custom-card">
    <Card
      title="Sentiment Analysis"
      imagesrc={sentiment}
      alttext="Sentiment analysis"
      text="Sentiment alaysis on Twitter data for major US Airlines using Naive Bayes, Random Forest, SVM, and Deep learning classifiers."
    />
    </a>
  </Grid>

  <Grid>
  <a href="https://github.com/dsahu040/Perceptron-Algorithm" target="_blank" class="custom-card">
    <Card
      title="Perceptron Algorithm"
      imagesrc={perceptron}
      alttext="Perceptron"
      text="Implementation of learing algorithm for the Perceptron in python. It takes two-dimensional input from a file."
    />
    </a>
    </Grid>

    <Grid>
    <a href="https://github.com/dsahu040/CPU-Process-Scheduler-in-C" target="_blank" class="custom-card">
    <Card
      title="CPU Process Scheduler"
      imagesrc={process}
      alttext="Process Scheduler"
      text="Implementation of a Process Scheduler for an Operating System, by implementing a Linked List in C."
    />
    </a>
    </Grid>

    <Grid>
    <a href="https://github.com/dsahu040/RNP-Calculator-in-C" target="_blank" class="custom-card">
    <Card
      title="RNP Calculator"
      imagesrc={calc}
      alttext="Calculator"
      text="Implementation of a Postfix-notation (RPN) Calculator using Stack and Hash Table data structures in C."
    />
    </a>
    </Grid>

    <Grid>
    <a href="https://github.com/dsahu040/Graph-Library-in-C"  target="_blank" class="custom-card">
    <Card
      title="Graph Library in C"
      imagesrc={graph}
      alttext="Graph"
      text="Implementation of a custom graph library that contains various graph functions in C."
      />
      </a>
      </Grid>
    </Grid>
      </div>
    )
  }
}

export default Projects;
